 /* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}

/**
 * FONTS
 */

@font-face {
  font-family: 'Silkscreen Web';
  src: url('../../public/fonts/slkscr-webfont.eot');
  src: url('../../public/fonts/slkscr-webfont.eot?#iefix') format('embedded-opentype'),
    url('../../public/fonts/slkscr-webfont.woff') format('woff'),
    url('../../public/fonts/slkscr-webfont.ttf') format('truetype'),
    url('../../public/fonts/slkscr-webfont.svg#silkscreennormal') format('svg');
  font-weight: normal;
  font-style: normal;
}

/**
 * GAME
 */

html {
  font-size: 16px;
}

body {
  font-family: 'Silkscreen Web', monotype;
  height: 100vh;
  width: 100%;
  background-color: black;
}

h1 {
  font-size: 2.5rem;
  margin-bottom: 1rem; 
  text-align: center;
}

#game {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

#pause {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

#youWin {
  font-size: 5vh;
}

#background {
  position: absolute;
  top: 50.9%;
  left: 52.5%;
  transform: translate(-48%, -48%);
  width: 1149px;
  height: 530px;
  border: 10px solid white;
}

#boardBack {
  opacity: 0;
}

#scoreBox {
  opacity: .7;
}

#pongMenuText {
  font-size: 75px;
  text-shadow: 2px 2px 3px black;
}

.vsMenuText {
  font-size: 25px;
  text-shadow: 2px 2px 3px black;
}

.vsMenuText2 {
  font-size: 20px;
  text-shadow: 2px 2px 3px black;
}

#background2 {
  top: 50%;
  left: 50%;
  transform: translate(50%, 50%);
}